$purple : #846ADD;
$grey : #666666;
input {
  height: 50px;
}
input, textarea {
  display: block;
  width: 100%;
  background-color: rgb(230, 230, 230);
  color: $grey;
  font-family: Montserrat-Bold !important;
  border-radius: 25px;
  padding-left: 55px;
  padding-right: 30px;
  border: none;
}
button {
  display: block;
  width: 100%;
}
.user {
  float: left;
  margin-left: 32px;
  margin-top: -42px;
  color: $grey;
}
.envelope {
  float: left;
  margin-left: 32px;
  margin-top: -41px;
  color: $grey;
}
.phone {
  float: left;
  margin-left: 32px;
  margin-top: -41px;
  color: $grey;
}
.txt {
  padding-left: 30px;
  padding-top: 13px;
}
.send {
  display: block;
  width: 100%;
  height: 45px;
  background-color: $purple;
  color: white;
  font-family: Montserrat-Bold;
  border-radius: 25px;
  border: none;
  cursor: pointer;
  transition: all 1s;
}
.send:hover {
  background: #212121;
}
input[type="text"], textarea, button {
  outline: none;
}


