@font-face {
  font-family: "Montserrat-Bold";
  src: url("/fontawesome/fonts/Montserrat/Montserrat-Bold.ttf");
  src: url("/fontawesome/fonts/Montserrat/Montserrat-Bold.ttf") format("ttf"),
  url("/fontawesome/fonts/Montserrat/Montserrat-Bold.ttf")  format("trueType");
}
* {
  margin: 0px;
  padding: 0px;
}
body {
  overflow-x: hidden;
  overflow-y: scroll;
}
html {
  background: url("/images/admin.jpg") no-repeat center center fixed;
  background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  -webkit-background-size: cover;
}



